"use server";

import { redirect } from "next/navigation";
import { revalidatePath } from "next/cache";
import { createSession, deleteSession } from "@/lib/auth";
import fs from "fs/promises";
import path from "path";
import type { Template } from "./definitions";

export type State = {
  error?: string;
  message?: string;
};

export async function authenticate(
  prevState: State | undefined,
  formData: FormData
) {
  try {
    const email = formData.get("email") as string;
    const password = formData.get("password") as string;
    
    if (email !== process.env.ADMIN_EMAIL) {
      return { error: "Invalid email address." };
    }

    if (password !== process.env.ADMIN_PASSWORD) {
      return { error: "Invalid password." };
    }

    await createSession(email);

  } catch (error) {
    if ((error as Error).message.includes("CredentialsSignin")) {
      return { error: "Invalid credentials." };
    }
    return { error: "Something went wrong." };
  }
  redirect("/admin");
}


export async function logout() {
  await deleteSession();
  redirect("/");
}

export async function uploadTemplate(prevState: State | undefined, formData: FormData) {
  const file = formData.get("templateFile") as File;
  if (!file || file.size === 0) {
    return { error: "Please select a file to upload." };
  }

  if (file.type !== "application/json") {
    return { error: "Invalid file type. Please upload a JSON file." };
  }

  try {
    const filePath = path.join(process.cwd(), 'src', 'lib', 'data', 'templates.json');
    
    // Read existing templates
    let templates: Template[] = [];
    try {
      const currentData = await fs.readFile(filePath, 'utf-8');
      templates = JSON.parse(currentData);
      if (!Array.isArray(templates)) {
        templates = [];
      }
    } catch (e) {
      // File might not exist or is empty, start with an empty array
      templates = [];
    }

    // Read and parse new template
    const newTemplateContent = await file.text();
    const newTemplate: Omit<Template, 'id'> = JSON.parse(newTemplateContent);

    // Add a unique ID
    const templateWithId: Template = {
      ...newTemplate,
      id: `uploaded-${Date.now()}-${Math.random().toString(36).substring(2, 9)}`,
      templateId: `uploaded-${Date.now()}-${Math.random().toString(36).substring(2, 9)}`,
    };

    // Add to list and write back
    templates.unshift(templateWithId); // Add to the beginning
    await fs.writeFile(filePath, JSON.stringify(templates, null, 2), 'utf-8');

    revalidatePath("/admin");
    return { message: "Template uploaded successfully!" };

  } catch (error) {
    console.error(error);
    if (error instanceof SyntaxError) {
      return { error: "Invalid JSON format in the uploaded file." };
    }
    return { error: "An unexpected error occurred while uploading the template." };
  }
}
